<template>
    <div class="my-popups">
        <div class="modal d-block ">
            <div class="modal-dialog modal-dialog-centered ui-draggable">
                <div class="modal-content">
                    <div class="modal-header ui-draggable-handle">
                        <button type="button" class="close" @click=closepopup()>
                            <em aria-hidden="true" class="fa fa-times"></em>
                        </button>
                        <h4 name="header" class="modal-title">
                            {{ $t('MakeProjectDefault') }}
                        </h4>
                    </div>
                    <div class="modal-body">
                        <dynamic-form lang="en" ref="defaultproject" :buttons="buttons" :schema="FormSchema"
                            v-on:OnSubmit="onSubmit">
                        </dynamic-form>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../services/DataService';
export default {

    props: {
        userdetails: {
            type: Object,
            required: true
        }
    },
    data() {
        return {
            FormSchema: [{
                layoutType: "double",
                Data: [

                    {
                        astype: "SelectField",
                        label: "Default project ",
                        name: "Project",
                        value: "",
                        placeholder: "select Project",
                        config: {
                            options: [],
                            onChange: this.SelectProject
                        },
                    },
                ]
            },


            ],
            buttons: [{
                type: "submit",
                class: "btn btn-success",
                text: "<i class='fa fa-save pr-2'></i>Share",
            },
            {
                type: "button",
                class: "btn btn-danger",
                text: "<i class='fa fa-close pr-2'></i>Cancel",
                onClick: this.closepopup
            }],
            getprojectlist: [],
            mode: 'tags',
            selectedprojectid: null
        }
    },
    created() {

        var vm = this;
        // console.log("userdetails", vm.userdetails);

        if (vm.userdetails.default_project_id != null) {

            vm.FormSchema[0].Data[0].value = vm.userdetails.default_project_id;
        }
        vm.getProjectdropdownlist();
    },
    methods: {
        closepopup() {
            this.$emit('closepopup')
        },
        SelectProject(event, field) {
            var vm = this;
            vm.selectedprojectid = null;
            vm.selectedprojectid = field.value;
        },

        getProjectdropdownlist() {

            var vm = this;
            let Url = `id=&moduleName=PROJECT&type=USER_PROJECTS&search=&refId=${vm.userdetails.user_id}`
            DataService.GetDDLData(Url).then(response => {
                if (response.data != '' && response.data != null) {
                    vm.getprojectlist = response.data.DATA;

                    vm.getprojectlist.forEach((item) => {
                        (vm.FormSchema)[0].Data[0].config.options.push({
                            value: item.value,
                            name: item.name
                        })
                    })

                    vm.$refs.defaultproject.UpdateKeyValue();
                }

            })
        },
        onSubmit(event, values) {

            var vm = this;
            var obj = {
                projectId: event.Project,
                users: vm.userdetails.user_id
            }
            var Jsondata = `postString=${JSON.stringify(obj)}`;
            //var params = `ProjectId=${vm.selectedprojectid}&UserID=${vm.userdetails.user_id}`
            DataService.MakeProjectDefaultForUsers(Jsondata).then(response => {
                if (response.data.status == 'Success') {
                    vm.ShowAlert('Project Added Successfully', "success", true, vm.$t("Alert"));
                    vm.$parent.FetchUserData();

                    vm.closepopup();
                }
            })
        }
    }
}
</script>