                                                                                            type="text"
                                                                                            v-model="item.second_value"
                                                                                            v-bind:id="item.label"
                                                                                            v-bind:name="'second' + item.label + index" />
                                                                                    </Field>
                                                                                    <Field
                                                                                        :name="'textarea' + item.label + index"
