                                                                                        v-slot="{ errors }">
                                                                                        <textarea
                                                                                            v-bind:class="{ 'form-control': true, 'is-invalid': item.dt_code == 'textarea' && errors[0] }"
                                                                                            v-model="item.value"
                                                                                            v-bind:id="item.label"
                                                                                            v-on:change="SetValues($event, item)"></textarea>
                                                                                    </Field>
                                                                                    <div class="form-control pl-4 border-0 bg-transparent"
                                                                                        v-if="item.dt_code == 'radio' || item.dt_code == 'toggle'">
                                                                                        <div
                                                                                            class="form-check form-check-inline customcheckinput">
                                                                                            <div class="form-check form-check-inline custom-control mx-4 pl-2 custom-radio"
                                                                                                v-for="(option, optionIndex) in MakeArray(item.picklist_options, item.dt_code)"
                                                                                                v-bind:key="option.name"
                                                                                                :key="optionIndex">

                                                                                                <Field
                                                                                                    :name="'rdo_' + item.field_Id + option.value"
                                                                                                    :rules="{ 'required': item.is_required }"
                                                                                                    :label="item.label"
                                                                                                    v-if="item.operator_expression != 'Is Null' && item.operator_expression != 'Is Not Null'">

                                                                                                    <input
                                                                                                        v-bind:id="'rdo_' + item.field_Id + option.value"
                                                                                                        class="dynamic form-check-input custom-control-input"
                                                                                                        v-model="item.value"
                                                                                                        v-bind:name="'rdo_' + item.field_Id + option.value"
                                                                                                        v-on:change="SetValues($event, item)"
                                                                                                        v-bind:data-resource="option.name"
                                                                                                        v-bind:value="option.value"
                                                                                                        type="radio"
                                                                                                        v-bind:checked="option.value == item.value" />
                                                                                                </Field>
                                                                                                <label
                                                                                                    v-if="item.operator_expression != 'Is Null' && item.operator_expression != 'Is Not Null'"
                                                                                                    class="custom-control-label universalradio-custom-control-label pl-0 pt-0 dynamic"
                                                                                                    v-bind:for="'rdo_' + item.field_Id + option.value">{{
                                                                                                        option.name
                                                                                                    }}</label>
                                                                                            </div>
                                                                                        </div>
                                                                                    </div>
                                                                                    <div class="form-control pl-0 border-0 bg-transparent"
