                                                                                                        v-model="item.select_value"
                                                                                                        v-bind:name="'chk_' + item.custom_field_id + option.value"
                                                                                                        v-bind:data-resource-id="option.name"
                                                                                                        v-bind:value="option.value"
                                                                                                        type="checkbox"
                                                                                                        v-on:change="SetValues($event, item)" />
                                                                                                </Field>
                                                                                                <label
                                                                                                    class="custom-control-label universal-custom-control-label pl-2 pr-1 dynamic"
