                                                                                        :rules="{ 'required': item.is_required }"
                                                                                        :label="item.label + index"
                                                                                        v-slot="{ errors }">
                                                                                        <Multiselect
                                                                                            :multiple="(item.operator_expression == 'IN' || item.operator_expression == 'NOT IN') ? true : false"
                                                                                            :name="'Multiselect' + item.label + index"
                                                                                            v-bind:class="{
                                                                                                'disableAria': true, 'is-invalid': item.picklist_options == 'Lookup' &&
                                                                                                    item.dt_code == 'select' && item.dropdown_type == 'custom' && errors[0]
                                                                                            }"
                                                                                            placeholder="Search a value"
                                                                                            v-model="item.select_value"
