                                                                                            :filter-results="false"
                                                                                            :searchable="true"
                                                                                            track-by="value"
                                                                                            :show-no-results="false"
                                                                                            :internal-search="false"
                                                                                            :clear-on-select="false"
                                                                                            :close-on-select="true"
                                                                                            :options="(item.dropdown_type == 'normal') ? MakeNormalArray(item.select_options) : item.select_options"
