                                                                                            @search-change="onSearch($event, item)"
                                                                                            :showNoOptions="true"
                                                                                            @select="setSelectedValue(item, $event)">
                                                                                            <span slot="noOptions">
                                                                                                Type to search {{
                                                                                                    $t(item.label,
                                                                                                        item.display_name) }}
                                                                                            </span>
                                                                                        </Multiselect>
                                                                                    </Field>

                                                                                    <Field
                                                                                        v-if="item.picklist_options == 'Lookup' && item.dt_code == 'select' && item.dropdown_type == 'normal' &&
                                                                                            (item.operator_expression != 'IN' && item.operator_expression != 'NOT IN') && item.operator_expression != 'Is Null' && item.operator_expression != 'Is Not Null'"
                                                                                        :name="'select' + item.label + index"
                                                                                        :rules="{ 'required': item.is_required }"
                                                                                        v-model="item.value"
                                                                                        v-slot="{ errors }">

                                                                                        <select
                                                                                            :name="'select' + item.label + index"
                                                                                            v-on:change="SetValues($event, item, index)"
                                                                                            v-bind:class="{ 'form-control disableAria': true, 'is-invalid': errors[0] }">
                                                                                            <option value>{{
                                                                                                $t("Select") }}</option>
                                                                                            <option
                                                                                                v-for="(option, optionKey) in MakeNormalArray(item.select_options)"
                                                                                                :key="optionKey"
                                                                                                v-bind:value="option.value">
                                                                                                {{ option.name }}
                                                                                            </option>
                                                                                        </select>
                                                                                    </Field>
                                                                                    <Field
                                                                                        v-if="item.picklist_options != 'Lookup' && item.dt_code == 'select' &&
                                                                                            (item.operator_expression != 'IN' && item.operator_expression != 'NOT IN')"
