                                                                                                v-bind:value="option.value">
                                                                                                {{ option.name }}
                                                                                            </option>
                                                                                        </select>
                                                                                    </Field>
                                                                                    <Field
                                                                                        v-if="item.picklist_options != 'Lookup' && item.picklist_options != null && item.dt_code == 'select' && (item.operator_expression == 'IN' || item.operator_expression == 'NOT IN')"
                                                                                        :name="'Multiselect2' + item.label + index"
                                                                                        :rules="{ 'required': item.is_required }"
                                                                                        label="name"
                                                                                        v-slot="{ errors }">
                                                                                        <Multiselect
                                                                                            v-bind:multiple="(item.operator_expression == 'IN' || item.operator_expression == 'NOT IN') ? true : false"
                                                                                            v-bind:taggable="(item.operator_expression == 'IN' || item.operator_expression == 'NOT IN') ? true : false"
                                                                                            v-bind:class="{
                                                                                                'disableAria': true, 'is-invalid': item.picklist_options == 'Lookup' &&
                                                                                                    item.dt_code == 'select' && item.dropdown_type == 'custom' && errors[0]
                                                                                            }"
                                                                                            v-model="item.select_value"
                                                                                            placeholder="Search a value"
                                                                                            :showNoOptions="true"
                                                                                            :filter-results="false"
                                                                                            :searchable="true"
                                                                                            track-by="value"
                                                                                            :show-no-results="false"
                                                                                            :internal-search="false"
                                                                                            :clear-on-select="false"
                                                                                            :close-on-select="true"
                                                                                            :options="MakeArray(item.picklist_options, item.dt_code)"
                                                                                            :object="true" label="name"
                                                                                            @search-change="onSearch($event, item)"
                                                                                            @select="setSelectedValue(item, $event)">
                                                                                            <span slot="noOptions">
                                                                                                Type to search {{
                                                                                                    $t(item.label,
                                                                                                        item.display_name) }}
                                                                                            </span>
                                                                                        </Multiselect>
                                                                                    </Field>
                                                                                </td>
                                                                                <td class="text-center single-action">
                                                                                    <a v-if="index != 0"
                                                                                        href="javascript:;"
                                                                                        v-on:click="DeleteSearchItem(item, index)">
                                                                                        <em
                                                                                            class="fa fa-trash action-icon text-danger"></em>
                                                                                    </a>
                                                                                </td>
                                                                            </tr>
                                                                        </tbody>
                                                                    </table>

                                                                </div>
                                                            </div>
                                                            <div class="col-12 float-left p-0 mt-2"
                                                                v-if="!isBlank(condition)">
                                                                <div class="alert alert-secondary py-2">
                                                                    <label class="font-weight-bold mb-0">{{
                                                                        $t("Condition") }} :</label>
                                                                    <label class="mb-0 ml-1">{{ condition }}</label>
                                                                </div>
                                                            </div>
                                                            <div class="col-12 float-left p-0 mt-2 d-none">
                                                                <div class="alert alert-secondary py-2">
                                                                    <label class="font-weight-bold mb-0">
                                                                        Database
                                                                        Condition:
                                                                    </label>
                                                                    <label class="mb-0 ml-1"> {{ databaseCondition }}
                                                                    </label>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                                <div class="modal-footer text-right pr-0 pb-0">
                                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 p-0">
                                                        <a href="javascript:;" :class="{ 'btn btn-success mr-2': true }"
                                                            v-on:click="SubmitData('save', values, meta)"
                                                            :title="$t('SaveAndSearch')">
                                                            <em class="fa fa-floppy-o pr-2" aria-hidden="true"></em>
                                                            {{ $t("SaveAndSearch") }}
                                                        </a>
                                                        <a href="javascript:;" class="btn btn-success mr-2"
                                                            v-on:click="SubmitData('search', values, meta)"
                                                            :title="$t('Search')">
                                                            <em class="fa fa-search pr-2" aria-hidden="true"></em>
                                                            {{ $t("Search") }}
                                                        </a>
                                                        <a href="javascript:;" class="btn btn-primary delete-btn"
                                                            v-on:click="showListData = true; showAddData = false; filterItemData = []; GetSearchFilterItemData();"
                                                            :title="$t('Cancel')">
                                                            <em class="fa fa-times pr-2" aria-hidden="true"></em>
                                                            {{ $t("Cancel") }}
                                                        </a>
                                                    </div>
                                                </div>
                                            </Form>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </transition>
</template>

