<template lang="en">
<section>
    <div class="my-popups">
        <div class="modal d-block ">
            <div class="modal-dialog modal-dialog-centered ui-draggable">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full  d-flex">
                        <h4 name="header" class="modal-title  pt-1">
                            {{ $t('AssignBonus') }}
                        </h4>
                        <span class="user-guide ">
                            <a class="ancuserguide" data-toggle="collapse" href="javascript:;" role="button" aria-expanded="false" style="color: white;" aria-controls="collapseExample">
                                <em></em>User Guide</a>
                            <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                                    <div class="con" v-html="$t(addUserGuide)"></div>
                                </div>
                            </div>
                        </span>
                        <button class="close" v-on:click="ClosePopUp"><em aria-hidden="true" class="fa fa-times"></em>
                        </button>
                    </div>

                    <loader :isVisible="isLoading"></loader>
                    <div class="listing py-3 px-3">
                        <dynamic-form lang="en" ref="refAssignBonus" :buttons="buttons" :schema="FormSchema"></dynamic-form>
                        <div class="listing">
                            <div class="table-responsive">
                                <tg-list :showCheckBox="false" :IsShowAction="true" :listType="['List']"
                                    ModuleName="CompanySetup" SubModuleCode="User" :PageSize="PageSize"
                                    IdentityColumn="BONUS_ID" :HeaderText="$t('AssetManagement')"
                                    :widgets="widgets" :ListData="GetUserAssignBonusList" :HeaderData="Header"
                                    :callbackfunction="FetchData" :LegendArray="LegendArray"
                                    :SearchFields="leftSearchFields" :SortExp="SortExp" :SortBy="SortBy"
                                    :NorecordfoundText="$t('NorecordfoundText')" :TotalRecords="TotalRecord"
                                    :RenderRowActionMethod="renderActions" :ListDataCallBackFunction="FetchData"
                                    v-on:PagerButtonClick="pagerMethod" v-on:HeaderButtonClick="commonHeaderButtonClick"
                                    v-on:SortdataButtonClick="sortdata" v-on:ActionButtonClick="actionButtonClick">

                                    <template v-slot:slotdata="{ data }">                                       
                                        <template v-if="data.column.COLUMN_NAME == 'MINUTES'">
                                            <span>
                                                 {{convertMinutesToHours(data.row.MINUTES) }}
                                            </span>
                                        </template>
                                    </template>
                                </tg-list>
                                <!-- :HeaderButtons="listheaderbuttons"  -->
                                <!-- convertMinutesToHours -->
                            </div>                          
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</section>
</template>

<script>
import moment from 'moment';
import DataService from '../../services/DataService';
export default {
    props: {
        GetUserAssignBonus: {
            type: Array
        },
        addUserGuide: {
            type: String,
            required: true
        }
    },
    data() {
        return {
            isLoading: false,
            GetUserAssignBonusList: [],
            leftSearchFields: '',
            listheaderbuttons: [],
            LegendArray: [],
            FormSchema: [{
                    layoutType: "triple",
                    Data: [{
                            astype: "SelectField",
                            label: this.$t('LeaveType'),
                            name: "LeaveType",
                            value: '',
                            placeholder: "Please Select",
                           // visibility: false,
                            validationRules: {"required": true},
                            config: {
                                options: [],
                                showAddIcon: false,
                            },
                        },
                        {
                            astype: "SelectField",
                            label: this.$t('Days'),
                            name: "SelectDays",
                            value: "",
                            placeholder: "Please Select",
                            visibility: true,
                            config: {
                                options: [{
                                    name: 1,
                                    value: 1
                                }, {
                                    name: 2,
                                    value: 2
                                }, {
                                    name: 3,
                                    value: 3
                                }, {
                                    name: 4,
                                    value: 4
                                }, {
                                    name: 5,
                                    value: 5
                                }],
                                showAddIcon: false,
                            },
                            validationRules: "",
                        },
                        {
                            astype: "DateField",
                            label: this.$t('Hours'),
                            name: "Hours",
                            value: "",
                            placeholder: "Please Select",
                            visibility: true,
                            config: {
                                mode: "time",
                                is24hr: true,
                               // format: "hh:mm",
                                
                            }
                        }
                    ]
                },

                {
                    layoutType: 'single',
                    Data: [{
                        astype: "TextAreaField",
                        label: this.$t('Description'),
                        name: "Description",
                        value: "",
                        visibility: true,
                        validationRules: { "required": false, max: 90 },
                        config: {
                            options: [],
                            showAddIcon: false,
                        }
                    }]
                }
            ],
            buttons: [{
                    type: "submit",
                    class: "btn btn-success save",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    onClick: this.SaveUserBonus
                },
                {
                    type: "submit",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Close",
                    onClick: this.ClosePopUp
                }
            ],
            assignBonusList: [], // New property to hold AssignBonus dat
            BONUS_ID: '',
            Header: [{
                "COLUMN_NAME": "Added_By",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("CreatedBy"),
                "DISPLAY_ORDER": 1,

            },
            {
                "COLUMN_NAME": "CREATED_AT",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("Create At"),
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "BONUS_LEAVE",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("No of bonus leaves(Days)"),
                "DISPLAY_ORDER": 3,
                //settings: {
                  //  isInSlot: true
                //}
            },
            {
                "COLUMN_NAME": "MINUTES",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("Bonus Leaves Hours"),
                "DISPLAY_ORDER": 4,
                settings: {
                   isInSlot: true
                }
            },
            {
                "COLUMN_NAME": "COMMENT",
                "DATA_TYPE": "text",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": this.$t("Description"),
                "DISPLAY_ORDER": 5,
            },                    

            ],
            TotalRecord:0,
            listActions:[ 
                    {
                        title: 'Edit',
                        href: 'javascript:void(0)',
                        callbackfunction: 'EditAssignBonus',
                        icon: 'fa fa-pencil text-success action-icon',
                        additionalSpan: ''
                    },  
                    {
                        title: 'Delete',
                        href: 'javascript:void(0)',
                        icon: 'fa fa-trash',
                        callbackfunction: 'DeleteAssignBonus',
                        additionalSpan: ''
                    },
                    {
                        title: 'Share',
                        callbackfunction: 'sharereport',
                        href: 'javascript:void(0)',
                        icon: 'fa fa-share-alt text-success action-icon',
                        additionalSpan: ''
                    },
            ],
        }
    },
    async created() {
        var vm = this;
        await vm.GetLeaveData();
         vm.FetchData();
    },
    methods: {
        ClosePopUp() {
            var vm = this;
            vm.$parent.CloseUserAssignBonus();
            
        },
         FetchData() {
            var vm = this;
            vm.isLoading = true;
            var url = `Id=${vm.GetUserAssignBonus.user_id}`;
             DataService.GetAssignBonusListing(url).then(function (response) {
                console.log(response);
                vm.isLoading = false;
                if (response.data != '' && response.data != null) {
                    if (response.data.Result.length > 0) {
                        console.log(response.data.Result);
                        vm.GetUserAssignBonusList = response.data.Result;
                    }
                }
            })
        },
        async GetLeaveData() {
            var vm = this;
            vm.isLoading = true;
            var params = `moduleName=User&type=LEAVES_TYPES`;
            await DataService.GetDDLData(params).then(response => {
                if (response.data.DATA.length > 0) {
                    response.data.DATA.forEach(item => {
                        vm.FormSchema[0].Data[0].config.options.push({
                            name: item.name,
                            value: item.value
                        })
                    })
                    vm.$refs.refAssignBonus.UpdateKeyValue();
                }
                vm.isLoading = false;
            }).catch(err => {
                console.log(err);
            })
        },
        SaveUserBonus() {
            var vm = this;
            vm.isLoading = true;
            const getDate = moment(vm.FormSchema[0].Data[2].value);
            var getminutes = getDate.hours() * 60 + getDate.minutes();
            if (vm.FormSchema[0].Data[2].value == '') {
                getminutes = 570;
            }
            const type = (typeof (vm.FormSchema[0].Data[0].value) == undefined || vm.FormSchema[0].Data[0].value == '') ? 50193 : vm.FormSchema[0].Data[0].value;
            const days = vm.FormSchema[0].Data[1].value == '' ? 1 : vm.FormSchema[0].Data[1].value;
            var UserBonus = type + "^" + days + "^" + 0 + "^" + 0 + "^" + 0 + "^" + vm.FormSchema[1].Data[0].value;
            var params = `bonus=${UserBonus}&bounceId=${vm.BONUS_ID}&id=${vm.GetUserAssignBonus.user_id}&min=${getminutes}`;
            DataService.SaveBonus(params).then(function (response) {
                if (response.data.status == 'Success' && response.data.code == '0') {
                    vm.FetchData();
                    vm.ShowAlert(vm.$t("AddedSuccessBonus"), "success", true, vm.$t('Alert'));                   
                     vm.isLoading = false;
                } else {
                    vm.ShowAlert(vm.$t('ErrorBonus'), "danger");
                    vm.isLoading = false;
                }              
            }).catch(err => {
                console.log(err);
                vm.isLoading = false;
            });
        },
        Edit: async function (Item) {
            var vm = this;
            vm.BONUS_ID = Item.BONUS_ID;
            (vm.FormSchema)[0].Data[0].value = Item.LEAVE_TYPE_ID;
            (vm.FormSchema)[0].Data[1].value = Item.BONUS_LEAVE;
            const minutes = Item.MINUTES;
            const HH = Math.floor(minutes / 60);
            const MM = minutes % 60;
            const period = HH >= 12 ? 'PM' : 'AM';
            const hour = HH % 12 || 12; // Convert to 12-hour format
            const formattedHH = hour.toString();
            const formattedMM = MM >= 10 ? MM.toString() : '0' + MM.toString();
            var value = `${formattedHH}:${formattedMM} ${period}`;
            const date = new Date();
            var formatTime = await vm.convertTo24HourFormat(value.toString())
            var formatDate = await vm.formatDateToYYYYMMDD(date)
            const timeZone = Intl.DateTimeFormat().resolvedOptions().timeZone;
            var MinutesValue = await vm.convertToDateString(formatTime, formatDate, timeZone);

            (vm.FormSchema)[0].Data[2].value = MinutesValue;
            //(vm.FormSchema)[0].Data[2].value = vm.convertMinutesToHours(Item.MINUTES);         
            (vm.FormSchema)[1].Data[0].value = Item.COMMENT;
        },
        convertTo24HourFormat: function (timeStr) {
            const [time, modifier] = timeStr.split(' ');
            const [hours, minutes] = time.split(':').map(Number);

            // Convert hours based on AM/PM
            let convertedHours = hours;
            if (modifier === 'PM' && hours !== 12) {
                convertedHours += 12;
            } else if (modifier === 'AM' && hours === 12) {
                convertedHours = 0; // Midnight case
            }
            const formattedHours = String(convertedHours).padStart(2, '0');
            const formattedMinutes = String(minutes).padStart(2, '0');

            // Return the time in 'HH:MM' format
            return `${formattedHours}:${formattedMinutes}`;
        },
        formatDateToYYYYMMDD: function (date) {
            const year = date.getFullYear();
            const month = String(date.getMonth() + 1).padStart(2, '0'); // Months are 0-based
            const day = String(date.getDate()).padStart(2, '0');

            return `${year}-${month}-${day}`;
        },
        convertToDateString: function (timeStr, dateStr, timeZone) {
            const date = new Date(`${dateStr}T${timeStr}:00`);

            // Convert to specified time zone
            const options = {
                timeZone: timeZone,
                weekday: 'short',
                year: 'numeric',
                month: 'short',
                day: 'numeric',
                hour: '2-digit',
                minute: '2-digit',
                second: '2-digit'
            };
            const formatter = new Intl.DateTimeFormat('en-US', options);
            return formatter.format(date);
        },
        convertMinutesToHours(minutes) {
            const hours = Math.floor(minutes / 60);
            const remainingMinutes = minutes % 60;
            return `${hours}h ${remainingMinutes}m`;
        },
        Delete: function (Item) {
            var vm = this;
            if (Item.BONUS_ID > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("BounsLeave")), function () {
                    var objData = {
                        BounsID: Item.BONUS_ID,
                        userid: vm.GetUserAssignBonus.user_id,
                        SatusID: 1003,
                    }
                    var jsonData = JSON.stringify(objData);
                    DataService.DeleteBounceLeave(jsonData).then( function (response) {
                        if (response.status == 200) {
                            vm.FetchData();
                            vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Record')]), "success", true, vm.$t("Alert"));                            
                            vm.isLoading = false;
                        } else {
                            vm.ShowAlert(vm.$t('DeletedError', [vm.$t('Record')]), "failure", true, vm.$t("Alert"));
                            vm.isLoading = false;
                        }
                    }, );
                });
            } else {
                vm.ShowAlert(vm.$t("Selectanyrecordtodelete"));
                vm.isLoading = false;
            }           

        },
        commonHeaderButtonClick: function () { },
        renderActions: function (row) {            
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    case 'Delete':
                        action.isVisible = true;
                        break;
                    case 'Share':
                        action.isVisible = false;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        }, 
        actionButtonClick: function (callbackfunction, item, index, event) {
            var vm = this;
            
            switch (callbackfunction) {
                case 'EditAssignBonus':
                    vm.Edit(item);
                    break;
                case 'DeleteAssignBonus':
                    vm.Delete(item);
                    break;                
            }
        },      
    }
}
</script>
