<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle py-1">
                            <h4 name="header" class="modal-title mt-2 pt-1">
                                {{ $t('assoLicenses') }}
                            </h4>
                            <button class="close" v-on:click="ClosePopUp"><em aria-hidden="true" class="fa fa-times"></em>
                            </button>
                        </div>
                        <div class="listing py-3 px-3">
                            <loader :is-visible="isLoading"></loader>
                            <div class="col-sm-12 col-md-12 col-lg-12 form-panel mt-0 p-0">
                                <div class="form-content">
                                    <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                        <div class="heading-border">
                                            <h5 class="h5"><span>All Licenses</span></h5>
                                        </div>
                                        <div class="managefolder py-3 px-2">
                                            <ul class="list-group" style="max-height: 400px;min-height: 200px;overflow-y: auto;">
                                                <li class="list-group-item single-action" v-for="item in GetAllLicenses">
                                                    <strong>{{ item.MODULE_NAME }} ({{ item.LICENSE_COUNT }})</strong>
                                                    <a :class="'aAddLicense float-right ' + item.disabledproperty" href="javascript:;" :moduleid="item.Module_Master_Id" v-on:click="AssociateDeAssociateLicense(item,'Associate')">
                                                        <i :class="'fa fa-plus-circle text-success action-icon ' + item.disabledproperty" alt="Click to Associate License" title="Click to Associate License"></i>
                                                    </a>
                                                </li>
                                            </ul>
                                        </div>
                                    </div>
                                    <div class="col-sm-12 col-md-6 col-lg-6 col-xl-6 float-left">
                                        <div class="heading-border">
                                            <h5 class="h5"><span>Added Licenses</span></h5>
                                        </div>
                                        <div class="managefolder py-3 px-2">
                                            <ul class="list-group" style="max-height: 400px;min-height: 200px;overflow-y: auto;">
                                                <li class="list-group-item single-action" v-for="Addeditem in GetAddedLicenses">
                                                    <strong>{{ Addeditem.MODULE_NAME }}</strong>
                                                    <a :class="GetAllAssociateData.is_default ? 'AddLicense float-right disabled' : 'AddLicense float-right'" href="javascript:;" :moduleid="Addeditem.MODULE_ID" v-on:click="AssociateDeAssociateLicense(Addeditem,'De-associate')">
                                                        <i :class="GetAllAssociateData.is_default ? 'fa fa-times-circle text-danger action-icon disabled' : 'fa fa-times-circle text-danger action-icon'" alt="Click to Deassociate License" title="Click to Deassociate License"></i>
                                                    </a>
                                                </li>
                                            </ul>
                                        </div>
                                    </div>
                                    <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12  popup-footer-button pl-0 pr-0">
                                        <div class="col-lg-6" v-if="GetAllAssociateData.is_default">
                                            <div class="py-2">
                                                <small>
                                                    <span class="mandatory">{{ $t("DefaultLicense") }}</span>
                                                </small>
                                            </div>
                                        </div>
                                        <div class="col-lg-6 text-right">
                                            <a href="javascript:;" title="" class="closeassoLicense btn btn-danger" @click="ClosePopUp"><i class="fa fa-close pr-2"></i>Close</a>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../services/DataService';
export default{
    props:{
        GetAllAssociateData:{
            type:Object
        }
    },
    data(){
        return{
            isLoading:false,
            GetAllLicenses:[],
            GetAddedLicenses:[],
            IsOnLoad:false
        }
    },
    async created(){
        var vm = this;
        await vm.GetAssociateLicenses(vm.IsOnLoad);
    },
    methods:{
        async GetAssociateLicenses(IsOnLoad){
            var vm = this;
            vm.isLoading = true;
            var params = `userType=${vm.GetAllAssociateData.UserTypeId}&createdUserId=${vm.GetAllAssociateData.user_id}`;
            await DataService.GetAssociateLicenses(params).then(response=>{
                vm.GetAllLicenses = response.data.allLicenses;
                vm.GetAddedLicenses = response.data.addedLicenses;
                if (vm.GetAddedLicenses !== undefined && vm.GetAddedLicenses.length > 0)
                    {
                vm.GetAddedLicenses.forEach(item=>{
                
                    var getAddedvalue = vm.GetAllLicenses.find(x=> x.Module_Master_Id === item.MODULE_ID);
                    if(getAddedvalue != undefined){
                        getAddedvalue.disabledproperty = 'disabled';
                    }
                })
            }
            else
                {
                    if(IsOnLoad){
                        vm.ShowAlert('Please Purchase more license by upgrade subscription!', "failure", true, vm.$t("Alert"));
                    }                    
                }
                vm.isLoading = false;
            }).catch(err=>{
                console.log(err);
            })
        },
        async AssociateDeAssociateLicense(data,AssociateValue){
            //debugger;
            var vm = this;
            vm.isLoading = true;
            var params = `moduleMasterID=${data.Module_Master_Id != undefined ? data.Module_Master_Id : data.MODULE_ID}&userId=${vm.GetAllAssociateData.user_id}&userType=${vm.GetAllAssociateData.user_type_id}&actionType=${AssociateValue}`;
            DataService.GetAssociateDeAssociateLicense(params).then(response=>{
                vm.GetAllLicenses = response.data.allLicenses;
                vm.GetAddedLicenses = response.data.addedLicenses;
                if (vm.GetAddedLicenses !== undefined && vm.GetAddedLicenses.length > 0)
                    {
                vm.GetAddedLicenses.forEach(item=>{
                    var getAddedvalue = vm.GetAllLicenses.find(x=> x.Module_Master_Id === item.MODULE_ID);
                    if(getAddedvalue != undefined){
                        getAddedvalue.disabledproperty = 'disabled';
                    }
                })
                }
                else
                {
                    vm.ShowAlert('Please Purchase more license by upgrade subscription!', "failure", true, vm.$t("Alert"));
                }

                vm.isLoading = false;
            }).catch(err=>{
                console.log(err);
            })
        },
        ClosePopUp(){
            var vm = this;
            vm.$parent.CloseAssociatePopUp();
        }
    }
}
</script>