                                                                            <td class="text-center">{{projectitem.project_name}}</td>
                                                                            <td class="text-center">{{projectitem.file_name}}</td>
                                                                            <td class="text-danger text-center">
                                                                                <a :href="projectitem.file_path" download :title="projectitem.file_name">
                                                                                <em class="fa fa-download action-icon text-success ml-5"></em></a>
                                                                            </td>
                                                                        </tr>
                                                                    </tbody>
                                                                </table>
                                                            </div>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </section>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../services/DataService';
export default {
    props:{
        GetUserDetailsData:{
            type: Array
        }
    },
    data() {
        return {
            getViewUserGeneralDetails:[],
            getViewUserConfigurationDetails:[],
            getViewUserActivitiesDetails:[],
            getViewUserMessageBoardDetails:[],
            getViewUserExpenseDetails:[],
            getViewUserDocStorageDetails:[],
            getViewUserProjectDetails:[],
        }
    },
    async created() {
        var vm = this;
        await vm.GetUserDetailByUserId();
    },
    methods: {
        ClosePopUp() {
            var vm = this;
            vm.$parent.CloseUserDetails();
        },
        async GetUserDetailByUserId(){
            var vm = this;
            var params = `Id=${vm.GetUserDetailsData.user_id}`;
            await DataService.GetUserDetailByUserId(params).then(response=>{
                if(response.data.result != null){
                    var getAllData = JSON.parse(response.data.result);
                    vm.getViewUserGeneralDetails = getAllData.LEFTDATA;
                    vm.getViewUserConfigurationDetails = getAllData.CONFIG_DATA;
                    vm.getViewUserActivitiesDetails = getAllData.ACTIVITIES;
                    vm.getViewUserMessageBoardDetails = getAllData.MESSAGEFILES;
                    vm.getViewUserExpenseDetails = getAllData.EXPENSEFILES;
                    vm.getViewUserProjectDetails = getAllData.PROJECTFILES;
                    vm.getViewUserDocStorageDetails = getAllData.STORAGEFILES;
                }
            }).catch(err=>{
                console.log(err);
            })
        }
    }
}
</script>