<template>
    <section>
        <loader :is-visible="isLoading"></loader>
        <tg-list ref="tglistuser" :IsShowAction="true" :showCheckBox="true" :listType="['List']" ModuleName="Company"
            SubModuleCode="CMP_USER" IdentityColumn="user_id" HeaderText="Manage Users" :ListData="GetUserListing"
            :HeaderData="Headers" :ViewList="ViewList" v-on:ApplyView="OnApplyView"
            v-on:RefreshManageView="FetchUserData" :callbackfunction="FetchUserData" :HeaderButtons="listheaderbuttons"
            :TotalRecords="TotalRecords" :SearchFields="leftSearchFields" :RenderRowActionMethod="renderActions"
            :IsFilterApplied="searchCondition ? true : false" :ListDataCallBackFunction="FetchUserData"
            :LegendArray="LegendArray" :SortExp="sortExp" :SortBy="sortBy" :NorecordfoundText="$t('NoRecordfound')"
            v-on:LeftsearchButtonClick="leftCommonSearch" v-on:PagerButtonClick="pagerMethod"
            v-on:HeaderButtonClick="commonHeaderButtonClick" v-on:ActionButtonClick="actionButtonClick"
            v-on:SortdataButtonClick="sortdata">
            <template v-slot:slotdata="{ data }">
                
                <template v-if="data.column.COLUMN_NAME == 'avatar'">
                    <div class="form-control p-0 border-0 text-left" alt="" title=""
                        v-if="data.row.avatar != null && data.row.avatar != ''">
                        <img :src='data.row.avatar' class="border p-2" height="50" width="100" title="" />
                    </div>
                    <div class="form-control p-0 border-0 text-left" v-else>
                        <img :src="'https://cogniterblob.blob.core.windows.net/' + GetUserInfo.ContainerName + '/Upload/UserProfileImage/' + GetUserInfo.Avatar"
                            alt="No Image Available" height="50" width="100" title="" />
                    </div>
                    
                </template>
                <template v-if="data.column.COLUMN_NAME == 'face_recog_setup'">
                    {{ data.row.face_recog_setup == true ? 'Added' : 'Not Added' }}

                </template>
                <template v-if="data.column.COLUMN_NAME == 'user_type_id'">{{ data.row.user_type_id != null ?
                    data.row.user_type_id : '--'}} </template>
                    <template v-if="data.column.COLUMN_NAME == 'user_type_id'"></template>
                <template v-if="data.column.COLUMN_NAME == 'department_id'">{{ data.row.department_id != null ?
                    data.row.department_id : '--' }}</template>
                <template v-if="data.column.COLUMN_NAME == 'designation_id'">{{ data.row.designation_id != null ?
                    data.row.designation_id : '--' }}</template>
                <template v-if="data.column.COLUMN_NAME == 'status_id'">
                    <td class="text-center single-action">
                        <a class="aUpdateStatus" data-toggle-tooltip="tooltip" data-original-title="User is Active"
                            v-on:click="ChangeUserStatus(data.row)" v-if="data.row.status_id == 'Active'">
                            <em class="fa fa-check text-success action-icon"></em>
                        </a>
                        <a v-else class="aUpdateStatus" data-toggle-tooltip="tooltip"
                            data-original-title="User is Active" v-on:click="ChangeUserStatus(data.row)">
                            <em class="fa fa-close text-danger action-icon"></em>
                        </a>
                    </td>
                </template>
                <template v-if="data.column.COLUMN_NAME == 'aduser_status' && ShowUserSyncStatus">
                    <td class="text-center single-action">
                        <a title="User has been synced on AD successfully" v-if="data.row.aduser_status == 1">{{
                            $t('UserAdSync') }}<i class="fa fa-random text-info action-icon disabled"></i></a>
                        <a v-else-if="data.row.aduser_status == 0" class="AdPassword"
                            v-on:click="OpenToSyncUser(data.row)" :title="data.row.aduser_reason"
                            :userId="data.row.user_id" :emailId="data.row.email_id" href="javascript:;"><i
                                class="fa fa-retweet text-primary action-icon"></i></a>
                        <a v-else class="AdPassword" :title="data.row.aduser_reason"
                            v-on:click="OpenToSyncUser(data.row)" :userId="data.row.user_id"
                            :emailId="data.row.email_id" href="javascript:;">Create vDesk
                            user</a>
                    </td>
                </template>
            </template>
        </tg-list>
        <modalSyncUser v-if="ShowUserSyncPopUp" :UserSyncData="UserSyncData"></modalSyncUser>
        <modalImportUser v-if="ShowImportUserPopUp"></modalImportUser>
        <modalAssociateLicense v-if="ShowAssociatePopUp" :GetAllAssociateData="GetAllAssociateData">
        </modalAssociateLicense>
        <modalUserDetails v-if="ShowUserDetails" :GetUserDetailsData="GetUserDetailsData"></modalUserDetails>
        <!-- <modalviewlayout v-if="ShowManageView" submodulecode="Cmp_User" modulename="User"
            @close="ShowManageView = false" v-bind:callbackfunction="fetchRecords" :usertype="usertype">
            <h5 slot="header" @GetViewId="GetViewId">{{ $t('Search') }}</h5>
        </modalviewlayout> -->
        <modalAssignUserBonus v-if="ShowUserAssignBonus" :GetUserAssignBonus="GetUserAssignBonus"
            addUserGuide="USER_GUIDE_ASSIGN_BONUS">
        </modalAssignUserBonus>
        <modalUserResetPasswordVue v-if="ShowUserResetPassword" :UserResetPasswordData="UserResetPasswordData"
            @CloseUserResetPasswordPopUp="CloseUserResetPasswordPopUp">
        </modalUserResetPasswordVue>

        <searchFilter v-if="showFilter" submodulecode="CMP_USER" modulename="Company" v-on:close="showFilter = false"
            v-bind:callbackfunction="FetchUserData" v-on:FilterData="FilterData">
            <h5 slot="header">{{ $t('Search') }}</h5>
        </searchFilter>

        <ManageView v-if="ShowManageView" submodulecode="CMP_USER" modulename="Company"
            @refreshViewDropdown="refreshViewDropdown" v-on:close="ShowManageView = false"
            v-bind:callbackfunction="FetchUserData" :usertype="usertype">
            <h5 slot="header">{{ $t('Search') }}</h5>
        </ManageView>
        <modalConfiguration v-if="ShowConfigurationPopUp" :GetConfiguration="GetConfiguration"></modalConfiguration>
        <manageprojectdefault v-if="projectdefault" @closepopup="closepopup" :userdetails="userdetails">
        </manageprojectdefault>
        <AssignManager v-if="isVisibleAssigManager" :UserId="selectUserId" :CurrentUserDetails="CurrentUserDetails">
        </AssignManager>
       
        <FaceRecognizer @close="showFaceRecognizerModal = false"  :id="userIdForfaceCapture"  :name="selectedUserName" v-if="showFaceRecognizerModal" @statusUpdate="updateRecord($event)"/>
      
    </section>
</template>
<script>

import DataService from '../../services/DataService';
import Schema from "../Users/UserListingSchema";
import modalSyncUser from '../../components/Modals/modalSyncUser.vue';
import modalImportUser from '../../components/Modals/modalImportUser.vue';
import modalAssociateLicense from '../../components/Modals/modalAssociateLicense.vue';
import modalUserDetails from '../../components/Modals/modelUserDetails.vue';
//import modalviewlayout from '../../components/Common/view-layout.vue';
import modalAssignUserBonus from '../../components/Modals/modalAssignUserBonus.vue';
import modalUserResetPasswordVue from '../../components/Modals/modalUserResetPassword.vue';

import ManageView from '../../components/Common/view-layout.vue'
import searchFilter from "../../components/Common/search-filter.vue";
import modalConfiguration from '../../components/Modals/modalConfiguration.vue';
import manageprojectdefault from '../../components/Modals/manageprojectdefault.vue'
import AssignManager from './AssignManager.vue';
import * as XLSX from 'xlsx';
import FaceRecognizer from "./FaceCapture.vue";
import { event } from 'jquery';

export default {
    components: {
        modalSyncUser, modalImportUser, modalAssociateLicense, modalUserDetails, modalAssignUserBonus, modalUserResetPasswordVue,
        ManageView,
        searchFilter,
        modalConfiguration,
        manageprojectdefault,
        AssignManager,
        FaceRecognizer,
    },
    data() {
        return {
            showFaceRecognizerModal: false,
            isLoading: false,
            noRecord: false,
            leftSearchFields: Schema.leftsearchSchema,
            listheaderbuttons: Schema.buttons,
            listActions: Schema.listActions,
            View_List: [],
            ViewList: [],
            ShowManageView: false,
            showquickviewModal: false,
            ViewListId: '',
            viewId: 0,
            searchCondition: '',
            searchConditionJson: '',
            showFilter: false,
            PageSize: 10,
            PageNumber: 1,
            GetUserListing: [],
            GetExcelListing: [],
            exportExcelSchema: [],
            Headers: [],
            TotalRecords: 0,
            TotalPages: 0,
            LegendArray: [
                {
                    class: 'legend-pending colorboxsquare alert-warning',
                    text: this.$t('UserAprovers')
                },
                {
                    class: 'legends_medium colorboxsquare alert-info',
                    text: this.$t('VirtualUser')
                },
                {
                    class: 'legends_profile colorboxsquare alert-danger',
                    text: this.$t('AdUserFailure')
                },
            ],
            ShowAssignBonus: false,
            ShowUserSyncStatus: false,
            ShowUserSyncPopUp: false,
            ShowImportUserPopUp: false,
            UserSyncData: {},
            ShowAssociatePopUp: false,
            GetAllAssociateData: {},
            UserResetPasswordData: {},
            ShowUserDetails: false,
            GetUserDetailsData: [],
            ShowUserAssignBonus: false,
            GetUserAssignBonus: [],
            ShowUserResetPassword: false,
            userid: null,
            usertype: '',
            companyId: null,
            sortExp: '',
            sortBy: 'CREATED_AT', //'user_id',
            ShowConfigurationPopUp: false,
            GetConfiguration: [],
            appliedFilter: [],           
            forcefully: false,
            projectdefault: false,
            isVisibleAssigManager: false,
            selectUserId: null,
            CurrentUserDetails: {},
            userIdForfaceCapture: null,
            selectedUserName: '',
        }
    },
    async created() {
        var vm = this;
        vm.userid = vm.GetUserInfo.ID
        vm.usertype = vm.GetUserInfo.UserType
        vm.companyId = vm.GetUserInfo.CompanyId;
        await vm.GetAllAddedLicensesUser();
        await vm.CheckExtraFeaturesUser();
        await vm.FetchUserData();
        await Schema.bindleftCommonSearchdropdown(vm, 'Company', 'STATUS_NAMES');
        await Schema.bindleftCommonSearchdropdown(vm, 'Company', 'DEPARTMENT');
        await Schema.bindleftCommonSearchdropdown(vm, 'Company', 'LOCATION');
        await Schema.bindleftCommonSearchdropdown(vm, 'Company', 'USER_TYPES');
        await Schema.bindleftCommonSearchdropdown(vm, 'Company', 'License');


        await vm.GetDdlRecords_ViewList();
    },
    mounted() {
        Schema.leftsearchSchema.forEach(item => {
            item.value = '';
            item.isSearch = false;
        })
    },
    methods: {
        updateRecord(event) {
            if(event.status=1){
          this.GetUserListing.find(x => x.user_id == event.userId).face_recog_setup = true;
        }
        },
        // updateRecord(event) {
        //   console.log("Event received from child:",this.GetUserListing, event);
        //   this.GetUserListing.find(x => x.user_id == event.userId).face_recog_setup = true;
          
        // },
        
        openFaceRecognizerModal(item) {
          
           this.selectedUserName= item.user_name;
      this.userIdForfaceCapture = item.user_id;

      this.showFaceRecognizerModal = true;
    },

        closepopup() {
            this.projectdefault = false;
        },
        commonDeleteData() {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            })
            if (val.length > 0) {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Folder")), function () {
                    vm.isLoading = true;
                    var params = `ids=${val}`;
                    DataService.DeleteUser(params).then(async function (response) {
                        if (response.status == 200) {
                            await vm.FetchUserData();
                            $('.chkItems:checkbox:checked,#chkAll_0').prop("checked", false).removeAttr('checked');
                            vm.listheaderbuttons[4].isdisabled = true;
                        }
                        vm.isLoading = false;
                    });
                });
            }
        },
        ChangeUserStatus(UserData) {
            debugger;
            var vm = this;
            var statusId = null;
            if (UserData.status_id == 'Inactive') {
                statusId = 1001
                const url = `id=${UserData.user_id}&statusId=${statusId}`;
                DataService.ChangeUserStatus(url).then(response => {
                    if (response.data.msg == "success") {
                       // vm.AssociateDeAssociateLicense(UserData,"De-associate");
                        vm.FetchUserData();
                        vm.ShowAlert(vm.$t('UserUpdated'), 'success');
                    }
                })
            }
            else {
                statusId = 1002
                vm.confirmR(vm.$t("ConfirmAssociateLicense"), true, (vm.$t("ConfirmAssociateLicense")), function () {
                    const url = `id=${UserData.user_id}&statusId=${statusId}`;
                    DataService.ChangeUserStatus(url).then(response => {
                        if (response.data.msg == "success") {
                            vm.AssociateDeAssociateLicense(UserData,"De-associate");
                            vm.FetchUserData();
                            vm.ShowAlert(vm.$t('UserUpdated'), 'success');
                        }
                    })
                })
            }
        },
        async AssociateDeAssociateLicense(item,associateValue) {
            //debugger;

            try {
                var vm = this;
                vm.isLoading = true;
                //var params = `moduleMasterID=0}&userId=${vm.UserId}&userType=${vm.UserTypeName}&actionType=${AssociateValue}`;
                var params = `moduleMasterID=0&userId=${item.user_id}&userType=${item.USER_TYPE_NAME}&actionType=${associateValue}&IsRemovedAll=true`;
                
                const response = await DataService.GetAssociateDeAssociateLicense(params);
                if (response.data != null) {
                    vm.isLoading = false;
                    
                         vm.isLoading = false;
                        vm.ShowAlert(vm.$t("DeAssociateLicenseSuccess"), "success", vm.$t('Alert'));
                    }

            } catch (error) {
                console.error('Error in AssociateDeAssociateLicense:', error);
            } finally {
                vm.isLoading = false;
            }
        },
        commonHeaderButtonClick(event) {
            switch (event.callbackfunction) {
                case "openFilterPopUp":
                    this.openFilterPopUp();
                    break;
                case "AddUser":
                    this.AddUser();
                    break;
                case "ImportUser":
                    this.ImportUser();
                    break;
                case "AssignManager":
                    this.AssignManager();
                    break;
                case 'ManageView':
                    this.ManageView();
                    break;
                case "DownloadExcelDataNew":
                    this.DownloadExcelDataNew();
                    break;
                case "commonDeleteData":
                    this.commonDeleteData();
                    break;
                case "removeFilter":
                    var ths = this;
                    ths.searchCondition = '';
                    const removeFilterindex = ths.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';
                    });
                    ths.listheaderbuttons[removeFilterindex].isvisible = false;
                    const openFilterPopUpindex = ths.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';
                    });
                    ths.listheaderbuttons[openFilterPopUpindex].iconClass = 'fa fa-filter';
                    try {
                        ths.appliedFilter = [];
                    } catch (e) { };
                    ths.FetchUserData();
                    break;
            }
        },

        AssignManager(event, item) {
            var vm = this;
            var val = "";
            $('.chkItems:checkbox:checked').not("[id^='chkAll_0']").each(function () {
                if (val.length > 0)
                    val += ',';
                val += $(this).val();
            })
            if (val.length > 0) {
                vm.CurrentUserDetails = vm.GetUserListing.find(x => x.user_id == val);
                vm.selectUserId = val;
                vm.isVisibleAssigManager = true;
            }
        },

        AddUser() {
            this.$router.push("/User/Add");
        },
        ImportUser() {
            var vm = this;
            vm.ShowImportUserPopUp = true;
        },
        CloseImportUserPopUp() {
            var vm = this;
            vm.ShowImportUserPopUp = false;
        },
        sortdata: function (s) {
            var SortDir = "sort tb_headerSortDown";
            this.sortBy = s;
            if (s === this.sortBy) {
                if (this.sortExp == "") {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                } else if (this.sortExp == "ASC") {
                    this.sortExp = "DESC";
                    SortDir = "sort tb_headerSortUp";
                } else {
                    this.sortExp = "ASC";
                    SortDir = "sort tb_headerSortDown";
                }
            }
            this.FetchUserData();
            $("#" + s + "  span:first-child").attr("class", SortDir);
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            var vm = this;
            switch (callbackfunction) {
                case 'addFace':
                     vm.openFaceRecognizerModal(item)
                    break;
                case 'editFace':
                     vm.openFaceRecognizerModal(item)
                    break;
                case 'ShowAssociateLicenses':
                    vm.ShowAssociateLicenses(item);
                    break;
                case 'ViewUserDetails':
                    vm.ViewUserDetails(item);
                    break;
                case 'AssignUserBonus':
                    vm.AssignUserBonus(item);
                    break;
                case 'ResetPassword':
                    vm.UserResetPasswordPopUp(item);
                    break;
                case 'SendMail':
                    vm.UserSendMail(item);
                    break;
                case 'SendMailInduction':
                    vm.UserSendMailInduction(item);
                    break;
                case 'SendFeedbackMail':
                    vm.UserSendFeedbackMail(item);
                    break;
                case "removeFilter":

                    vm.searchCondition = '';
                    const removeFilterindex = vm.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';
                    });
                    vm.listheaderbuttons[removeFilterindex].isvisible = false;
                    const openFilterPopUpindex = vm.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';
                    });
                    vm.listheaderbuttons[openFilterPopUpindex].iconClass = 'fa fa-filter';
                    try {
                        vm.appliedFilter = [];
                    } catch (e) { };
                    vm.FetchUserData();
                    break;
                case 'ConfigurationPopUp':
                    vm.ConfigurationPopUp(item);
                    break;
                case 'WorkLoad':
                    vm.WorkLoad(item);
                    break;
                case 'DefaultProject':
                    vm.DefaultProduct(item);
                    break;
            }
        },
        renderActions: function (row) {
            var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'View':
                        action.isVisible = true;
                        break;
                    case 'Assign Bonus':
                        action.isVisible = self.ShowAssignBonus;
                        break;
                    case 'Reset Password':
                        action.isVisible = true;
                        break;
                    case 'Send Credentials':
                        action.isVisible = true;
                        break;
                    case 'Configuration':
                        action.isVisible = true;
                        break;
                    case 'Work Load':
                        action.isVisible = row.USER_COUNT > 0 ? true : false;
                        break;
                        case 'Face recognition':
                        action.isVisible = row.face_recog_setup==true ? false : true;
                        break;
                        case 'Edit Face recognition':
                        action.isVisible =row.face_recog_setup ==true  ? true : false;
                        break;
                    case 'Associate Licenses':
                        action.isVisible = true;
                        break;
                    case 'Induction Email':
                        action.isVisible = true;
                        break;
                    case 'Feedback Email':
                        action.isVisible = true;
                        break;
                    case 'Default Product':
                        action.isVisible = true;
                        break;
                    default:
                        action.isVisible = true;
                        break;
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        DefaultProduct(item) {
            this.projectdefault = true;
            this.userdetails = item;
        },
        clickEventOfColumn(item) {
            var vm = this;
            // vm.$router.push({
            //     path: "/User/update/" + item.user_id
            // });
        },
        ShowAssociateLicenses(data) {
            debugger
            var vm = this;
            if (data.status_id == "Inactive") {
                vm.ShowAlert(vm.$t("This user is currently inactive. Please activate the user before assigning a license."), 'failure', true, vm.$t('Alert'));
                return;
            }
            vm.GetAllAssociateData = data;
            vm.ShowAssociatePopUp = true;
        },
        ViewUserDetails(data) {
            var vm = this;
            vm.ShowUserDetails = true;
            vm.GetUserDetailsData = data;
        },

        UserSendMail(data) {
            var vm = this;
            vm.isLoading = true;
            var params = `userId=${data.user_id}&emailId=${data.email_id}&forcefully=true`;
            DataService.SendMail(params).then(response => {
                if (parseInt(response.data.code) == 1) {
                    vm.ShowAlert(vm.$t("EmailSuccessfullySend"), 'success', true, vm.$t('Alert'));
                } else if (parseInt(response.data.code) == 2) {
                    vm.ShowAlert(vm.$t("CompanyNotSMTPCredentials"), 'failure', true, vm.$t('Alert'));
                } else if (parseInt(response.data.code) == -1) {
                    vm.ShowAlert(vm.$t(response.data.msg), 'failure', true, vm.$t('Alert'));
                }
                vm.isLoading = false;
            });
        },
        UserSendMailInduction(data) {
            var vm = this;
            vm.isLoading = true;
            var params = `userId=${data.user_id}&emailId=${data.email_id}&forcefully=false`;
            DataService.SendMailInduction(params).then(response => {
                if (parseInt(response.data.code) == 1) {
                    vm.ShowAlert(vm.$t("EmailSuccessfullySend"), 'success', true, vm.$t('Alert'));
                } else if (parseInt(response.data.code) == 2) {
                    vm.ShowAlert(vm.$t("CompanyNotSMTPCredentials"), 'failure', true, vm.$t('Alert'));
                } else if (parseInt(response.data.code) == -1) {
                    vm.ShowAlert(vm.$t(response.data.msg), 'failure', true, vm.$t('Alert'));
                }
                vm.isLoading = false;
            });
        },
        UserSendFeedbackMail(data) {
            var vm = this;
            vm.isLoading = true;
            var params = `userId=${data.user_id}&emailId=${data.email_id}&forcefully=false`;
            DataService.SendFeedbackMail(params).then(response => {
                if (parseInt(response.data.code) == 1) {
                    vm.ShowAlert(vm.$t("EmailSuccessfullySend"), 'success', true, vm.$t('Alert'));
                } else if (parseInt(response.data.code) == 2) {
                    vm.ShowAlert(vm.$t("CompanyNotSMTPCredentials"), 'failure', true, vm.$t('Alert'));
                } else if (parseInt(response.data.code) == -1) {
                    vm.ShowAlert(vm.$t(response.data.msg), 'failure', true, vm.$t('Alert'));
                }
                vm.isLoading = false;
            });
        },

        CloseUserDetails() {
            var vm = this;
            vm.GetUserDetailsData = [];
            vm.ShowUserDetails = false;
        },
        AssignUserBonus(data) {
            var vm = this;
            // if (JSON.parse(data.shift_id).value != null) {
            if (data.shift_id != null && data.shift_id != '' && typeof (data.shift_id) != 'undefined') {
                vm.GetUserAssignBonus = data;
                vm.ShowUserAssignBonus = true;
            } else {
                vm.ShowAlert(vm.$t('ErrorShift'), "warning");
            }
        },
        CloseUserAssignBonus() {
            var vm = this;
            vm.GetUserAssignBonus = []
            vm.ShowUserAssignBonus = false;
        },
        CloseAssociatePopUp() {
            var vm = this;
            vm.ShowAssociatePopUp = false;
        },
        ConfigurationPopUp(data) {
            var vm = this;
            vm.GetConfiguration = data;
            vm.ShowConfigurationPopUp = true;
        },
        CloseConfiguration() {
            var vm = this;
            vm.GetConfiguration = [];
            vm.ShowConfigurationPopUp = false;
        },
        UserResetPasswordPopUp(value) {
            var vm = this;
            vm.UserResetPasswordData = value
            vm.ShowUserResetPassword = true;
        },
        CloseUserResetPasswordPopUp() {
            var vm = this;
            vm.ShowUserResetPassword = false;
        },

        getRequestParams: function (pageNum, pageSize, sortBy, sortExp, isPaged, searchCondition) {
            let params = {};
            if (pageNum) { params["pageNum"] = pageNum; }
            if (searchCondition) { params["searchCondition"] = searchCondition; }
            if (isPaged) { params["isPaged"] = isPaged; }
            if (sortBy) { params["sortBy"] = sortBy; }
            if (sortExp) { params["sortExp"] = sortExp; }
            if (pageSize) { params["pageSize"] = pageSize; }
            return params;
        },

        async FetchUserData() {
            debugger;
            var vm = this;
            vm.isLoading = true;
            const params = this.getRequestParams(vm.PageNumber, vm.PageSize, vm.sortBy, vm.sortExp, vm.isPaged, vm.searchCondition);

            // var params = `search=&searchQuery=${vm.searchCondition.trim()}
            // &pageSize=${vm.PageSize}&pageNum=${vm.PageNumber}&sortBy=${vm.sortBy}
            // &sortExp=${vm.sortExp}&module=Company&submodules=Cmp_User`


            params.pageSize = vm.PageSize;
            params.pageNum = vm.PageNumber;
            params.searchQuery = vm.searchCondition;
            if (vm.PageNumber) { params.pageNum = vm.PageNumber; }
            else { vm.PageNumber = params.pageNum = 1; }
            if (typeof vm.isPaged != 'undefined') { params.isPaged = vm.isPaged; }
            else { vm.isPaged = params.isPaged = 1; }
            params.sortBy = vm.sortBy;
            params.sortExp = vm.sortExp;
            params.search = vm.search;
            params.module = 'Company';
            params.submodules = 'CMP_USER';

            var srchCondition = "";
            if (!this.isBlank(this.searchCondition)) {
                srchCondition = srchCondition + this.searchCondition;
            }
            if (!this.isBlank(this.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
            }
            if (srchCondition != "") {
                // Adding "AC." at the beginning of srchCondition


                params.searchCondition = srchCondition;
                vm.searchCondition = srchCondition;
                if (vm.appliedFilter.length > 0) {
                    const removeFilterindex = vm.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';
                    });
                    vm.listheaderbuttons[removeFilterindex].isvisible = true;
                    const openFilterPopUpindex = vm.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';
                    });
                    vm.listheaderbuttons[openFilterPopUpindex].iconClass = 'fa fa-filter text-success';
                }
            }


            await DataService.UserListing(params).then(response => {

                if (response.data.result.length > 0) {
                    var GetReponse = JSON.parse(response.data.result);
                    if (GetReponse.data.length > 0 && GetReponse.schema.length > 0) {
                        ///const filteredColumnNames = ["user_name", "user_emp_id", "role_id", "user_type_id", "email_id", "designation_id", "department_id", "leaves_taken", "total_leaves", "status_id", "aduser_status"];
                        //vm.Headers = GetReponse.schema.filter(x => filteredColumnNames.includes(x.COLUMN_NAME));
                        vm.Headers = GetReponse.schema;
                        //console.log('Headers', vm.Headers);
                        vm.Headers.forEach(hitem => {
                            hitem.settings = null;
                            if (hitem.COLUMN_NAME == 'user_name') {
                                hitem.settings = {
                                    clickEvent: vm.clickEventOfColumn,
                                    formatter: vm.columnDataFormatter,
                                };
                            }
                            if (hitem.COLUMN_NAME == 'avatar') {
                                hitem.settings = {
                                    isInSlot: true,
                                }
                            }
                            if (hitem.COLUMN_NAME == 'user_type_id' || hitem.COLUMN_NAME == 'department_id' || hitem.COLUMN_NAME == 'designation_id' || hitem.COLUMN_NAME == 'status_id' || hitem.COLUMN_NAME == 'aduser_status' || hitem.COLUMN_NAME == 'face_recog_setup') {
                                hitem.settings = { isInSlot: true }
                            }
                            // var getheaders = vm.Headers.find(x => x.CustomFieldId == hitem.CustomFieldId);
                            // if (getheaders.COLUMN_NAME == 'aduser_status') {
                            //     getheaders.DISPLAY_NAME = vm.$t('ActiveDirectoryUserSync');
                            // } else if (getheaders.COLUMN_NAME == 'role_id') {
                            //     getheaders.DISPLAY_NAME = vm.$t('UserRole');
                            // } else {
                            //     getheaders.DISPLAY_NAME = getheaders.DISPLAY_NAME != null ? vm.$t(getheaders.DISPLAY_NAME) : vm.$t(getheaders.COLUMN_NAME);
                            // }
                        })
                        vm.GetUserListing = GetReponse.data;
                        //console.log('GetUserListing', vm.GetUserListing);

                        vm.GetUserListing.forEach((row) => {
                            row.isCheckBoxDisabled = false;
                            if (row.is_default ?? true) {
                                if ((row.APPROVERCOUNT == 0 && row.LEADCOUNT == 0) && (row.is_default!=true)) {
                                    row.isCheckBoxDisabled = false;
                                }
                                else {
                                    row.isCheckBoxDisabled = true;
                                }
                            }
                            row.rowDetailsUrl = '';
                            if (row.rowDetailsUrl == '') {
                                row.rowDetailsUrl = '/User/update/' + row.user_id;
                            }
                            row.additionalClass = '';


                            if (row.APPROVERCOUNT != 0) {
                                row.additionalClass = "dev_expenseperday high-bar";
                            }
                            else if (row.LEADCOUNT > 0) {
                                row.additionalClass = "dev_expenseperday high-bar";
                            }
                            //  if (ViewBag.ExtraFeatureForAdd == "Yes" && row.ADUSER_STATUS == "0") { rowClass = "danger-bar"; }
                            else if (row.ADUSER_STATUS == "0") { row.additionalClass = "danger-bar"; }

                            if (row.is_sample_data == true) {
                                row.additionalClass = 'sample-bar';
                            }

                            if (row.is_virtual == true) {
                                row.additionalClass = 'usr_virtual medium-bar';
                            }
                        })

                        // vm.GetUserListing.forEach(item => {
                        //     item.UserTypeId = JSON.parse(item.user_type_id).value
                        //     item.user_type_id = JSON.parse(item.user_type_id).Name
                        //     item.designation_id = JSON.parse(item.designation_id).Name;
                        //     item.department_id = JSON.parse(item.department_id).Name;
                        //     item.status_id = JSON.parse(item.status_id).Name;
                        //     item.role_id = JSON.parse(item.role_id).Name != null ? JSON.parse(item.role_id).Name : "--";
                        // })
                        vm.TotalRecords = vm.GetUserListing[0].total_records;
                        vm.TotalPages = Math.ceil(vm.TotalRecords / vm.PageSize);
                        vm.CurrentPage = vm.PageNumber;
                        vm.isLoading = false
                        vm.noRecord = false;
                    } else {
                        vm.GetUserListing = [];
                        vm.noRecord = true;
                        vm.TotalRecords = 0;
                        vm.isLoading = false;
                    }
                } else {
                    vm.GetUserListing = [];
                    vm.noRecord = true;
                    vm.TotalRecords = 0;
                    vm.isLoading = false;
                }
            }).catch(err => {
                console.log(err);
            })
        },
