        async DownloadExcelDataNew() {
            var vueObj = this;
            vueObj.isLoading = true;
            vueObj.PageSize = vueObj.TotalRecords;
            await vueObj.FetchExcelData();
            vueObj.PageSize = 10;
            var dataArray = [];
            try {
                var userListingArray = vueObj.GetExcelListing;

                userListingArray.forEach(function (item) {
                    var newObj = Object.assign({});
                    vueObj.exportExcelSchema.forEach(function(header){
                        newObj[vueObj.$t(header.DISPLAY_NAME)] = item[header.COLUMN_NAME] || '';
                    });
                    // newObj['User Name'] = item.user_name || '';
                    // newObj['Employee Id'] = item.user_emp_id || '';
                    // newObj['Email'] = item.email_id || '';
                    // newObj['Designation'] = item.designation_id || '';
                    // newObj['Department'] = item.department_id || '';
                    dataArray.push(newObj);
                });
            } catch (e) {
                console.error("Invalid JSON string in GetExcelListing:", e);
            }

            if (dataArray.length > 0) {
                // Create a new worksheet
                const worksheet = XLSX.utils.json_to_sheet([]);

                // Create a new workbook
                const workbook = XLSX.utils.book_new();

                // Create a title row with "User Report" and date-time
                const currentDateTime = new Date().toLocaleString('en-US');
                const titleRow = [
                    ['User Report', '', '', '', currentDateTime] // Span from A1 to E1
                ];

                // Append title to the worksheet at the first row (A1 to E1)
                XLSX.utils.sheet_add_aoa(worksheet, titleRow, { origin: 'A1' });

                // Merge cells from A1 to D1 for the title
                worksheet['!merges'] = [
                    { s: { r: 0, c: 0 }, e: { r: 0, c: 3 } } // Merges A1:D1
                ];

                // Set styles for the title and date-time row
                worksheet['A1'].s = {
                    font: { bold: true, size: 20, color: { rgb: "000000" } }, // Increased size to 16
                    alignment: { horizontal: "left" },
                };

                worksheet['E1'].s = {
                    font: { bold: true, size: 20, color: { rgb: "000000" } }, // Increased size to 14
                    alignment: { horizontal: "right" },
                };

                // Add headers manually in A2
                const header = Object.keys(dataArray[0]);
                XLSX.utils.sheet_add_aoa(worksheet, [header], { origin: 'A2' });

                // Append the data starting from A3
                XLSX.utils.sheet_add_json(worksheet, dataArray, { origin: 'A3', skipHeader: true });

                // Apply styles to the header row (A2)
                const range = XLSX.utils.decode_range(worksheet['!ref']);
                for (let C = range.s.c; C <= range.e.c; ++C) {
                    const cell_address = XLSX.utils.encode_cell({ c: C, r: 1 }); // Header row index (1-based)
                    if (!worksheet[cell_address]) continue;
                    worksheet[cell_address].s = {
                        font: { bold: true, size: 18, color: { rgb: "000000" } }, // Increased size to 12
                        fill: { fgColor: { rgb: "D3D3D3" } }, // Gray background
                        alignment: { horizontal: "center" },
                    };
                }

                // Adjust column widths
                const colWidths = header.map(col => ({
                    wpx: Math.max(
                        col.length * 10, // Base width (adjust as needed)
                        ...dataArray.map(row => (row[col] || '').toString().length * 10) // Calculate max width
                    )
                }));

                worksheet['!cols'] = colWidths;

                // Append the worksheet to the workbook
                XLSX.utils.book_append_sheet(workbook, worksheet, "User List");

                // Save the workbook to a file
                XLSX.writeFile(workbook, "User-List.xlsx");
            } else {
                console.error("No data available to export");
            }
            vueObj.isLoading = false;
        },

        async FetchExcelData() {
            debugger;
            var vm = this;
            vm.isLoading = true;
            const params = this.getRequestParams(vm.PageNumber, vm.PageSize, vm.sortBy, vm.sortExp, vm.isPaged, vm.searchCondition);
            params.pageSize = vm.PageSize;
            params.pageNum = vm.PageNumber;
            params.searchQuery = vm.searchCondition;
            if (vm.PageNumber) { params.pageNum = 1 }
            else { vm.PageNumber = params.pageNum = 1; }
            if (typeof vm.isPaged != 'undefined') { params.isPaged = vm.isPaged; }
            else { vm.isPaged = params.isPaged = 1; }
            params.sortBy = vm.sortBy;
            params.sortExp = vm.sortExp;
            params.search = 'xls';
            params.module = 'Company';
            params.submodules = 'CMP_USER';

            var srchCondition = "";
            if (!this.isBlank(this.searchCondition)) {
                srchCondition = srchCondition + this.searchCondition;
            }
            if (!this.isBlank(this.conditionForInstantSearch)) {
                srchCondition = srchCondition + ((!this.isBlank(this.searchCondition)) ? (" AND " + this.conditionForInstantSearch) : this.conditionForInstantSearch);
            }
            if (srchCondition != "") {
                // Adding "AC." at the beginning of srchCondition


                params.searchCondition = srchCondition;
                vm.searchCondition = srchCondition;
                if (vm.appliedFilter.length > 0) {
                    const removeFilterindex = vm.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'removeFilter';
                    });
                    vm.listheaderbuttons[removeFilterindex].isvisible = true;
                    const openFilterPopUpindex = vm.listheaderbuttons.findIndex(object => {
                        return object.callbackfunction === 'openFilterPopUp';
                    });
                    vm.listheaderbuttons[openFilterPopUpindex].iconClass = 'fa fa-filter text-success';
                }
            }

            await DataService.UserListing(params).then(response => {
                debugger;
                if (response.data.result.length > 0) {
                    var GetReponse = JSON.parse(response.data.result);
                    if (GetReponse.data.length > 0 && GetReponse.schema.length > 0) {
                        vm.GetExcelListing = GetReponse.data;
                        vm.exportExcelSchema = GetReponse.schema;
                    } else {
                        vm.GetExcelListing = [];
                    }
                }
            }).catch(err => {
                console.log(err);
            })
        },
        WorkLoad(item) {
            var vm = this;
            vm.$router.push({ name: 'ResourceWorkLoad', params: { UserData: item } });
