<template>
    <section>
        <div class="my-popups">
            <div class="modal d-block ">
                <div class="modal-dialog modal-dialog-centered ui-draggable">
                    <div class="modal-content">
                        <div class="modal-header ui-draggable-handle py-1">
                            <h4 name="header" class="modal-title mt-2 pt-1">
                                {{ $t('Import') }}
                            </h4>
                            <button class="close" v-on:click="ClosePopUp"><em aria-hidden="true"
                                    class="fa fa-times"></em>
                            </button>
                        </div>
                        <div class="listing py-3 px-3">
                            <loader :is-visible="isLoading"></loader>
                            <div class="border p-3" v-if="isDownloadsample">
                                <div class="row">
                                    <div class="col-lg-4">
                                        <div class="form-group">
                                            <label>{{ $t('DownloadSampleFile') }}:</label>
                                            <div class="input-group mb-3">
                                                <div class="form-control">
                                                    <i class="fa fa-file-excel-o pr-1 text-success"></i>{{ $t('DownloadUserSample') }}.xls
                                                </div>
                                                <div class="input-group-append downloadExpense">
                                                    <span class="input-group-text" id="basic-addon2">
                                                        <a href="https://cogniterblob.blob.core.windows.net/global/SampleFiles/sample-file-user-import.xls" class="" download>
                                                            <i class="fa fa-download text-primary"></i>
                                                        </a>
                                                    </span>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div class="col-lg-4">
                                        <div class="form-group">
                                            <label>{{ $t('UploadFile') }}:<span class="text-danger"></span></label>
                                            <div class="input-group">
                                                <div class="custom-file expenseFile">
                                                    <input type="file" name='FileSrc' id='flAvatar' multiple="true"
                                                        tabindex="-1" v-on:change="setFile($event)" accept=".xlsx, .xls"
                                                        style="position: absolute; clip: rect(0px, 0px, 0px, 0px);">
                                                    <div class="bootstrap-filestyle input-group">
                                                        <input type="text" class="form-control disabled" disabled=""
                                                            v-model="attachmentName" />
                                                        <div class="input-group-append" style="cursor:pointer;">
                                                            <span
                                                                class="group-span-filestyle input-group-btn input-group-text bg-white"
                                                                tabindex="0">
                                                                <label title="Click here to select a file."
                                                                    for='flAvatar' class="mb-0 font-weight-normal ">{{
                                                                        $t('FileControlText') }}</label>
                                                            </span>
                                                            <span
                                                                class="input-group-text border-0 bg-transparent p-0 pl-2">
                                                                <a href="javascript:;" @click="RemoveUserFile()"
                                                                    class="round-icon-small btn-danger-light"
                                                                    title="Clear">
                                                                    <i class="fa fa-undo"></i>
                                                                </a>
                                                            </span>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                                <div class="row flex-row-reverse">
                                    <div class="col-lg-6 text-right">
                                        <a href="javascript:void(0);" @click="UploadUserFile()"
                                            :title="$t('ImportFromExcelFile')" class="btn btn-info formbtn mr-1"><i
                                                class="fa fa-file-excel-o mr-2"></i>{{ $t('ImportFromExcelFile') }}</a>
                                    </div>
                                </div>
                            </div>
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0"
                                v-if="isImportFromExcel">

                                <div class="col-lg-12 mb-3 p-0">
                                    <div class="heading-border">
                                        <h5 class="h5">
                                            <span id="divModuleHeaderName">Bulk Upload User</span>
                                        </h5>
                                    </div>
                                </div>
                                <ValidationObserver ref="FilterForm" v-slot="{ handleSubmit }">
                                    <form @submit.prevent="handleSubmit(UploadBulkUserData)" ref="FilterForm1">
                                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0">
                                            <div class="listing">
                                                <div class="table-responsive" id="divUserList">
                                                    <table id="tblUserList"
                                                        class="table table-bordered dt-responsive nowrap mt-0">
                                                        <thead class="thead-bg">
                                                            <tr>
                                                                <th style="width: 12%; min-width:200px;">
                                                                    <span>{{ $t('FIRSTNAME') }}</span>
                                                                </th>
                                                                <th style="width: 10%; min-width:200px;">
                                                                    <span>{{ $t('LASTNAME') }}</span>
                                                                </th>
                                                                <th style="width: 13%; min-width:200px;">
                                                                    <span class="float-left">{{ $t('UserType')
                                                                        }}</span><span
                                                                        class="text-danger float-left">*</span>
                                                                </th>
                                                                <th style="width: 15%; min-width:200px;">
                                                                    <span class="float-left">{{ $t('Email')
                                                                        }}</span><span
                                                                        class="text-danger float-left">*</span>
                                                                </th>
                                                                <th style="width: 2%" class="text-center">
                                                                    <span>Action</span>
                                                                </th>
                                                            </tr>
                                                        </thead>
                                                        <tbody v-if="noRecord">
                                                            <tr style="cursor:initial;">
                                                                <td colspan="4">
                                                                    <div
                                                                        class="text-center text-danger d-flex align-items-center justify-content-center norecs">
                                                                        {{ $t("NoRecordfound") }}</div>
                                                                </td>
                                                            </tr>
                                                        </tbody>
                                                        <tbody v-else>
                                                            <tr v-for="(item, index) in jsonData" :key="index">
                                                                <td>
                                                                    <input type="text" class="form-control"
                                                                        v-model="item['First Name']" />
                                                                </td>
                                                                <td>
                                                                    <input type="text" class="form-control"
                                                                        v-model="item['Last Name']" />
                                                                </td>
                                                                <td>
                                                                    <Field :name="'Field' + index"
                                                                        v-slot="{ field, errors, errorMessage, meta }"
                                                                        rules="required">
                                                                        <select v-bind="field"
                                                                            :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                            v-model="item.usertypeid"
                                                                            :key="MainComponentkey">
                                                                            <option v-for="inneritem in getUserArray"
                                                                                :value="inneritem.value">{{
                                                                                    inneritem.name }}</option>
                                                                        </select>
                                                                    </Field>
                                                                </td>
                                                                <td>
                                                                    <Field :name="'StringFirstValue' + index"
                                                                        v-slot="{ field, errors, errorMessage }"
                                                                        rules="required|email">
                                                                        <input type="text"
                                                                            :class="{ 'form-control': true, 'is-invalid': errors[0] }"
                                                                            v-model="item.Email" />
                                                                    </Field>
                                                                </td>
                                                                <td class="text-center">
                                                                    <a id="deleteUser-0" class="delete single-action" @click="deleteBulkuser(index)"
                                                                        title="">
                                                                        <i
                                                                            class="fa fa-trash text-danger action-icon"></i>
                                                                    </a>
                                                                </td>
                                                            </tr>

                                                        </tbody>
                                                    </table>
                                                </div>
                                            </div>
                                            <div class="clearfix"></div>
                                            <div class="row flex-row-reverse mb-2 mt-2">
                                                <div class="col-lg-12 text-right">
                                                    <button type="submit" class="btn btn-success formbtn text-white mr-2"
                                                        title=""><span class="fa fa-save pr-2"></span>{{
                                                            $t('ConfirmSubmit') }}</button>
                                                    <a href="javascript:;" class="btn btn-danger formbtn" @click="ClosePopUp"
                                                        title=""><i class="fa fa-close pr-2"></i>{{ $t('Cancel') }}</a>
                                                </div>
                                            </div>
                                        </div>
                                    </form>
                                </ValidationObserver>
                            </div>
                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left p-0" v-if="isUserSavedSuccessfully">
                                <div class="listing">
                                    <div class="table-responsive" id="divUserList">
                                        <table id="tblUserList" class="table table-bordered dt-responsive nowrap mt-0">
                                            <thead class="thead-bg">
                                                <tr>
                                                    <th style="width: 12%; min-width:200px;">
                                                        <span>{{ $t('UserName') }}</span>
                                                    </th>
                                                    <th style="width: 10%; min-width:200px;">
                                                        <span>{{ $t('Email') }}</span>
                                                    </th>
                                                    <th style="width: 10%; min-width:200px;">
                                                        <span>{{ $t('Import_Status') }}</span>
                                                    </th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr v-for="(item, index) in AfterBulkUploadData" :key="index">
                                                    <td>
                                                        {{ item.first_name }} {{ item.last_name }}
                                                    </td>
                                                    <td>
                                                        {{ item.Email_Id }}
                                                    </td>
                                                    <td v-if="item.import_status == 'duplicate'" class="text-center text-white">
                                                        <div style="background-color:rgb(215, 36, 53); color: rgb(255, 255, 255)">
                                                            <span class="w-100"></span>{{ item.import_status.toUpperCase() }}</div>
                                                    </td>
                                                    <td v-if="item.import_status == 'success'" class="text-center text-white">
                                                        <div style="background-color:rgb(40, 167, 69); color:rgb(255, 255, 255)">
                                                            <span class="w-100"></span>{{ item.import_status.toUpperCase() }}</div>
                                                    </td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import DataService from '../../services/DataService';
import * as XLSX from 'xlsx';
import { ValidationObserver, Form, ValidationProvider as Field, extend, ErrorMessage } from 'vee-validate';
import * as rules from 'vee-validate/dist/rules';
export default {
    setup() {
        Object.keys(rules).forEach(rule => {
            extend(rule, rules[rule]);
        });
    },
    components: {
        Form, Field, ValidationObserver
    },
    data() {
        return {
            isLoading: false,
            file: null,
            excelFileName: null,
            attachmentName: '',
            isImportFromExcel: false,
            jsonData: [],
            getUserArray: [],
            isUserSavedSuccessfully:false,
            AfterBulkUploadData:[],
            isDownloadsample:true,
            noRecord:false
        }
    },
    created() {
    },
    methods: {
        setFile: function (e) {
            this.attachmentName = e.target.files[0].name;
            this.file = e.target.files[0];
            this.excelFileName = e.target.files[0].name;

            const file = e.target.files[0];
            const reader = new FileReader();

            reader.onload = (r) => {
                const data = new Uint8Array(r.target.result);
                const workbook = XLSX.read(data, { type: 'array' });

                const sheetName = workbook.SheetNames[0];
                const sheet = workbook.Sheets[sheetName];

                // Convert sheet to JSON
                this.jsonData = XLSX.utils.sheet_to_json(sheet);
                this.jsonData.forEach((x,y)=>{
                    if(x.Email == undefined){
                        this.jsonData.splice(y,1);
                    }
                })
            };

            reader.readAsArrayBuffer(file);
        },
        async UploadUserFile() {
            var vm = this;
            await vm.GetUserTypeData();
            vm.isImportFromExcel = true;
            vm.isDownloadsample = false;
            vm.isUserSavedSuccessfully = false;
        },
        RemoveUserFile() {
            var vm = this;
            document.getElementById('flAvatar').value = "";
            vm.attachmentName = '';
            vm.file = null;
            vm.excelFileName = null;
        },
        async GetUserTypeData() {
            var vm = this;
            let url = `moduleName=Company&type=USER_TYPES`;
            await DataService.GetDDLData(url).then(response => {
                debugger
                if (response.data != null && response.data.DATA.length > 0) {
                    vm.getUserArray = response.data.DATA;
                    vm.getUserArray.forEach(item => {
                        vm.jsonData.forEach(inneritem => {
                            if (item.name == inneritem['User Type']) {
                                inneritem.usertypeid = item.value;
                            }
                        })
                    })
                }
            })
        },
        UploadBulkUserData() {
            var vm = this;
            var tempdata = [];
            if(vm.noRecord){
                vm.ShowAlert(vm.$t('NoRecordfound'), 'failure');
                return false;
            }
            vm.jsonData.forEach(item=>{
                tempdata.push({
                    FirstName: item['First Name'],
                    LastName:item['Last Name'],
                    UserType:item['User Type'],
                    EmailId:item.Email,
                    UserTypeId:item.usertypeid
                })
            })
            DataService.UploadBulkUser(tempdata).then(response=>{
                var getresult = JSON.parse(response.data.result.result.result);
                if (getresult.length > 0) {
                    vm.AfterBulkUploadData = getresult;
                    vm.isUserSavedSuccessfully = true;
                    vm.isImportFromExcel = false;
                    vm.isDownloadsample = false;
                    vm.ShowAlert(vm.$t('RecordsSavedSuccessfully'), 'success');
                }
            }).catch(err=>{
                console.log(err);
            })
        },
        ClosePopUp() {
            var vm = this;
            vm.$parent.CloseImportUserPopUp();
        },
        deleteBulkuser(index){
            var vm = this;
            vm.jsonData.splice(index,1);
            if(vm.jsonData.length == 0){
                vm.noRecord = true;
            }else{vm.noRecord = false;}
        }
    }
}
</script>